<template>
  <div id="app">
    <div class="inputs">
      <input v-model.number="number_colors" />
      <input v-for="i in [...Array(number_colors).keys()]" :key="`col-${i}`" type="color" v-model="colors[i]" />
      <input type="range" v-model="square_size" min="10" max="500" />
      <input type="range" v-model="offset" min="0" max="100" />
      <button @click="setCombs">GO</button>
    </div>
    <div id="cartela" :style="{position:'relative'}">
      <div v-for="(group, groupIndex) in groups" :key="groupIndex" class="div" :style="{'page-break-inside': 'avoid'}">
        <div v-for="(g, gi) in group" :key="gi" class="div" :style="{width: '100vw'}">
          <color-square v-for="(comb, index) in g" :key="index" :colors="comb" :size="square_size" :offset="offset" :style="{margin: `${offset}px`, 'margin-right': `${offset * (comb.length - 1)}px`, 'margin-bottom': `${offset * (comb.length - 1)}px`}"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ColorSquare from './components/ColorSquare.vue'

export default {
  name: 'App',
  components: {
    ColorSquare
  },
  data() {
    return {
      groups: [],
      colors: [],
      square_size: 200,
      offset: 10,
      number_colors: 8,
    }
  },
  watch: {
    number_colors(val) {
      this.colors.length = val;
    }
  },
  mounted() {
    this.colors = ["blue", "red", 'yellow', 'green', 'fuchsia', 'grey', 'cyan', 'pink'];
  },
  methods: {
    setCombs: function() {
      this.groups = this.combinations(this.colors);
    },
    combinations: function(str) {
        var fn = function(active, rest, a) {
            if (!active.length && !rest.length)
                return;
            if (!rest.length) {
                a.push(active);
            } else {
                fn(active.concat(rest[0]), rest.slice(1), a);
                fn(active, rest.slice(1), a);
            }
            return a;
        }
        let combs = fn([], str, []);
        combs = combs.filter(c => c.length !== 1);
        combs = combs.sort((x, y) => {
          if (x.length < y.length) return -1;
          if (x.length > y.length) return 1;
          return 0;
        })
        let groups = [];
        for (let i = 0; i < this.number_colors; i++){
          groups.push([]);
          for (let j = 0; j < this.number_colors - 1; j++){
            groups[i].push([]);
          }
        }
        combs.forEach(comb => {
          let colorIndex = this.colors.indexOf(comb[0]);
          groups[colorIndex][comb.length - 2].push(comb);
        });
        for (let i = 0; i < groups.length; i++){
          groups[i] = groups[i].filter(a => a.length > 0);
        }
        return groups;
    },
  }
}
</script>

